@import url(https://fonts.googleapis.com/css2?family=DM+Sans&family=Mulish&family=PT+Serif&family=Poppins&family=Zen+Kaku+Gothic+New&display=swap);
@import url(https://fonts.googleapis.com/css2?family=DM+Sans&family=Mulish&family=PT+Serif&family=Poppins&family=Zen+Kaku+Gothic+New&display=swap);
@import url(https://fonts.googleapis.com/css2?family=DM+Sans&family=Mulish&family=PT+Serif&family=Poppins&family=Zen+Kaku+Gothic+New&display=swap);
@import url(https://fonts.googleapis.com/css2?family=DM+Sans&family=Mulish&family=PT+Serif&family=Poppins&family=Zen+Kaku+Gothic+New&display=swap);
.body {
    background-color: rgb(24, 23, 23);
    color: #fff;
    font-family: 'Mulish';
    overflow: hidden;
}
/*navigation bar*/
.sectionOneTitle {
    text-align: center;
}


/*recommanded movies*/
.mainShowcase {
    width: 375vw;
    display: flex;
    transition: transform .5s;
}
.loading {
    color: #fff;
}
.movieLink {
    text-decoration: none;
    color: #fff;
}
.movieBox {
    position: relative;
    width: 25vw;
    z-index: 1;
    box-shadow: .3em .3em 1em #fff;
    border-radius: 1em;
    margin: 2em 2em;
}
.movieBox:hover {
    box-shadow: .3em .3em 2em #fff;
    transition: .3s;
}
.movieImage {
    width: 100%;
    border-radius: .8em .8em 0 0;
}
.movieLink h1 {
    text-align: center;
    font-size: 20px;
    padding: 0;
    padding-top: 1em;
}

/*moving button*/
.moveLeft, .moveRight {
    position: absolute;
    top: 60%;
    z-index: 2;
    font-size: 50px;
    color: rgba(255, 255, 255, 0.7);
    background: rgba(0, 0, 0, 0.4);
    border: none;
    border-radius: .5em;
    cursor: pointer;
}
.moveLeft {
    left: 2em;
}
.moveRight {
    right: 2em;
}

/*search by genre*/
.sectionTwoTitle {
    text-align: center;
}
.movieByGenres form {
    margin-top: 5em;
    text-align: center;
}
.movieByGenres input {
    width: 500px;
    height: 40px;
    text-align: center;
    border: none;
    border-radius: 1em;
    background: rgba(255, 255, 255, 0.1);

}
.movieByGenres input:-ms-input-placeholder {
    font-size: 25px;
}
.movieByGenres input::placeholder {
    font-size: 25px;
}
.movieByGenres input[type=text] {
    font-size: 25px;
    color: rgba(255, 255, 255, 0.4);
}
.movieByGenres input:focus {
    outline: none;
}
.genresEmpty {
    text-align: center;
    font-size: 25px;
    margin: 2em 1em;
    padding: 10em 0;
    border-top: 2px solid #fff;
}
.genreContainer {
    display: flex;
    flex-wrap: wrap;
    margin: 2em 1em;
    border-top: 2px solid #fff;
    justify-content: center;
}
.movieGenreBox {
    width: 20vw;
    text-align: center;
    box-shadow: .3em .3em 1em #fff;
    border-radius: 1em;
    margin: 2em 1em;
}
.movieGenreBox:hover {
    box-shadow: .3em .3em 2em #fff;
    transition: .3s;
}
.movieGenreImage {
    width: 100%;
    border-radius: 1em 1em 0 0;
}
.movieLink h1 {
    font-size: 20px;
    margin: 0;
    padding-bottom: 1em;
}

@media only screen and (max-width: 1025px) {
    .movieBox {
        margin: 2em 1em;
    }
    .movieLink h1 {
        font-size: 18px;
    }

    .moveLeft, .moveRight {
        top: 50%;
    }
}

@media only screen and (max-width: 769px) {
    .mainShowcase {
        width: 495vw;
    }
    .movieBox {
        width: 33vw;
        margin: 2em 1em;
    }
    .movieLink h1 {
        font-size: 15px;
    }

    .movieGenreBox {
        width: 25vw;
    }
}

@media only screen and (max-width: 426px) {
    .mainShowcase {
        width: 1500vw;
    }
    .movieBox {
        width: 100vw;
        margin: 2em 5em;
    }
    .movieLink h1 {
        font-size: 18px;
        padding-bottom: 0;
    }

    .moveLeft {
        left: 1em;
    }
    .moveRight {
        right: 1em;
    }

    .movieByGenres input {
        width: 350px;
    }
    .movieGenreBox {
        width: 60vw;
    }
}

@media only screen and (max-width: 376px) {
    .movieBox {
        margin: 2em 3em;
    }

    .movieByGenres input {
        width: 300px;
    }
    .movieGenreBox {
        width: 80vw;
    }
}

@media only screen and (max-width: 321px) { 
    .movieBox {
        margin: 2em 1.5em;
    }

    .movieByGenres input {
        width: 280px;
    }
}
.movie {
    background-color: rgb(24, 23, 23);
    color: #fff;
    font-family: 'Mulish';
}
.movieInfo {
    display: flex;
    padding: 3em;
}
.movieInfo img {
    padding-right: 3em;
}
.movieSummary { 
    padding: 3em;
    border: 2px solid #fff;
    width: 60vw;
}
.movieTitle {
    font-size: 60px;
    margin: 0;
    padding-bottom: 2em;
}
.movieYear, .movieRuntime, .movieGenre {
    font-size: 25px;
}
.movieGenre {
    list-style-type: none;
    padding: 0;
}

/*movie suggest*/ 
.suggestTitle {
    text-align: center;
    font-size: 40px;
}
.movieSuggest {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.movieSuggestdata {
    width: 15vw;
    box-shadow: .3em .3em 1em #fff;
    border-radius: 1em;
    margin: 4em 3em;
}
.movieSuggestdata:hover {
    box-shadow: .3em .3em 2em #fff;
    transition: .3s;
}
.movieSuggestdata img {
    width: 100%;
    border-radius: .8em .8em 0 0;
}
.suggestMovieTitle {
    text-align: center;
    font-size: 20px;
    color: #fff;
}
.movieSuggestLink {
    text-decoration: none;
}

.loading {
    text-align: center;
    font-family: 'Mulish';
    font-size: 60px;
    color: rgb(255, 255, 255);
    background-color: rgb(24, 23, 23);
    height: 100vh;
    margin: 0;
}

@media only screen and (max-width: 1025px) {
    .movieSuggestdata {
        width: 20vw;
        margin: 4em 1em;
    }
}

@media only screen and (max-width: 769px) {
    .movieInfo {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .movieInfo img {
        padding-right: 0;
        width: 560px;
    }

    .movieSuggestdata {
        width: 40vw;
        margin: 4em 1em;
    }
}

@media only screen and (max-width: 426px) {
    .movieInfo img {
        width: 355px;
    }
    .movieTitle {
        font-size: 40px;
    }
    .movieYear, .movieRuntime, .movieGenre {
        font-size: 20px;
    }

    .movieSuggestdata {
        width: 70vw;
        margin: 4em 1em;
    }
}

@media only screen and (max-width: 376px) {
    .movieInfo img {
        width: 325px;
    }
}

@media only screen and (max-width: 321px) {
    .movieInfo img {
        width: 292px;
    }
}
.logContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: rgb(24, 23, 23);
}

.loginOut {
    margin-top: 2em;
}

h2 {
    font-size: 30px;
    color: #fff;
    text-align: center;
    margin-top: 7em;
    font-family: 'Mulish';
}
.navbar {
    background-color: rgb(0, 0, 0);
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: 'Mulish';
}
.homeTitle {
    margin-left: 18px;
    font-size: 40px;
    color: rgb(255, 29, 29);
    cursor: pointer;
    text-decoration: none;
}
.signTitle {
    margin-right: 2em;
    font-size: 20px;
    border-radius: 1em;
    border: 2px solid rgb(0, 0, 0);
    background-color: #fff;
    padding: .3em .5em;
    text-decoration: none;
}
.signTitle:hover {
    background-color: rgb(0, 0, 0);
    color: #fff;
    transition: .3s;
    border: 2px solid rgb(255, 255, 255);
}


@media only screen and (max-width: 426px) {
    .signTitle {
        font-size: 15px;
    }
}
